.work-card{display:flex;width:1400px;height:180px;margin:5px;background-color:#0b132b;border-radius:25px;transition:.2s}@media(max-width: 768px){.work-card{width:90vw;margin:2px}}.work-card .left-container{flex:.25}.work-card .left-container .work-img{height:160px;width:300px;margin:8px 10px;border-radius:25px}@media(max-width: 768px){.work-card .left-container .work-img{height:150px;width:70px;border-radius:50%;margin:5px}}.work-card .right-container{flex:.75;font-family:"Poppins",sans-serif;display:relative}.work-card .right-container .work-head{margin-top:8px}@media(max-width: 768px){.work-card .right-container .work-head{margin-top:5px}}.work-card .right-container .work-head h2{font-size:18px;font-weight:700;color:#fff}@media(max-width: 768px){.work-card .right-container .work-head h2{font-size:12px;margin-left:10px;position:absolute}}.work-card .right-container .work-head h2 span{margin-left:10px;font-size:14px;font-weight:400;color:#858dad}@media(max-width: 768px){.work-card .right-container .work-head h2 span{display:block;font-size:10px;margin-left:0}}.work-card .right-container .work-items{margin-top:8px}@media(max-width: 768px){.work-card .right-container .work-items{margin-top:35px;width:100%;height:70%}}.work-card .right-container .work-items ul .work-item{color:#fff;letter-spacing:.8;font-size:14px}@media(max-width: 768px){.work-card .right-container .work-items ul .work-item{font-size:10px}}.work-card .right-container .work-items ul .work-item:before{content:"★";padding-right:8px;color:crimson}@media(max-width: 768px){.work-card .right-container .work-items ul .work-item:before{font-size:10px;padding:5px}}.work-card:hover{transform:scale(0.95);opacity:.9}